.root {
  font-family: "Gilroy";
  font-weight: bold;
  font-size: 22px;
  line-height: 27px;
  height: 50px;
  background: linear-gradient(276.53deg, #2e2eed -93.34%, #4e4ef9 82.6%);
  box-shadow: inset 0px -2px 0px rgba(0, 0, 0, 0.135107);
  border-radius: 25px;
  padding-left: 25px;
  padding-right: 25px;
  padding-top: 9px;
  padding-bottom: 9px;
  min-width: 225px;
