@import "sass/index";
@import "config/themes/_mines";

.optionsWrapper {
  background-color: use(--template-gameCell-controller);
  border-right-color: 0px 0px 6px 6px;
  & > div {
    margin-top: 1px;
    margin-bottom: 10px;
    padding-top: 10px;
    padding-bottom: 10px;
  }
}
.statisticsWrapper {
  margin-top: 10px;
}
.game {
  display: flex;
  background: var(--template-gameBlock-background);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  border-radius: 6px 6px 0 0;
  font-weight: 500;
  & > div {
    flex-basis: 50%;
  }
  padding-top: 30px;
  padding-bottom: 30px;
  @media (max-width: 1040px) {
    flex-direction: column-reverse;
    & > div:first-child {
      margin-top: 20px;
    }
  }
}
