@import "sass/index";
@import "config/themes/_mines.scss";

$indent: 20px;
$button-color: rgb(72, 53, 234);

@mixin flexItems($element) {
  display: flex;
  & > #{$element} {
    flex-basis: 50%;
  }
  & > #{$element}:first-child {
    margin-right: $indent;
  }
}

.root {
  & > div:not(:last-child) {
    margin-bottom: $indent;
  }

  & input {
    &:disabled {
      color: use(--template-input-topLabel-color);
    }
  }
}

.indicators {
  @include flexItems(div);
}

.buttons {
  @include flexItems(button);
  justify-content: center;

  & button {
    color: use(--template-input-topLabel-color);
    // background: transparent !important;
    font-family: "Gilroy";
    font-weight: bold;
    font-size: 15px;
    background-color: rgba($button-color, 0.6);
    padding-top: 9.5px;
    padding-bottom: 9.5px;
    border-radius: 8px;
    &:hover {
      background-color: rgba($button-color, 0.3);
    }
    &:active {
      background-color: rgba($button-color, 0.3);
    }
  }
  & button:first-child {
    background-color: transparent;
    border: 1px solid rgba($button-color, 0.3);
    text-transform: capitalize;
    &:hover {
      background-color: rgba($button-color, 0.3);
    }
  }
}
