.buttons {
  display: flex;
  text-align: center;
  justify-content: space-around;
  margin-bottom: 20px;
  @media (max-width: 1400px) {
    flex-direction: column;
    & > div:first-child {
      margin-bottom: 10px;
    }
  }
}

.betControl {
  display: grid;
  grid-template-areas:
    "mines mines"
    "totalBet totalBet"
    "numberOfBets numberOfBets"
    "onWin onLoss"
    "stopOnProfit stopOnLoss";
  gap: 20px;
  grid-template-columns: 1fr 1fr;
  @media (max-width: 1230px) {
    grid-template-areas:
      "mines" "totalBet" "numberOfBets" "onWin" "onLoss"
      "stopOnProfit" "stopOnLoss";
    grid-template-columns: 1fr;
  }
}

.manualControl {
  grid-template-areas: "mines" "totalBet";
  grid-template-columns: 1fr;
}

.mines {
  grid-area: mines;
}

.totalBet {
  grid-area: totalBet;
}

.onWin {
  grid-area: onWin;
}

.onLoss {
  grid-area: onLoss;
}

.numberOfBets {
  grid-area: numberOfBets;
}

.stopOnProfit {
  grid-area: stopOnProfit;
}

.stopOnLoss {
  grid-area: stopOnLoss;
}

.warningModal {
  background: #01213a;
}
