@import "sass/index";
@import "config/themes/_mines.scss";

.input {
  border: 1px solid $elephant;
  position: relative;
  overflow: hidden;
  background: use(--template-anyContent-background);
}

.button {
  background: $elephant;
}

.refreshIcon {
  & > path {
    fill: $elephant;
  }
}
