$desktop-gap: 10px;
$mobile-gap: 5px;
$mobileGridTemplate: repeat(5, calc((90vw - (#{$mobile-gap} * 4)) / 5));

.root {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.field {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(5, 1fr);
  gap: $desktop-gap;
  width: 30vw;
  height: 30vw;
  max-width: 580px;
  max-height: 580px;
  @media (max-width: 1040px) {
    width: 50vw;
    height: 50vw;
  }

  @media (max-width: 950px) {
    width: 90vw;
    height: 90vw;
  }

  @media (max-width: 490px) {
    grid-template-columns: $mobileGridTemplate;
    grid-template-rows: $mobileGridTemplate;
    gap: $mobile-gap;
  }
}

.rewardWindow {
  filter: drop-shadow(0px 5.31447px 30px rgba(171, 205, 255, 0.2))
    drop-shadow(0px 0px 30px rgba(142, 181, 239, 0.3));
  background: linear-gradient(
    135.94deg,
    #5dc4d9 1.59%,
    rgba(93, 196, 217, 0.4) 50.28%,
    #5dc4d9 100%
  );
  transition: opacity 0.2s;
  &:hover {
    opacity: 0.8;
  }
}
