@import "sass/_colors";

@mixin arrow-style {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: $elephant;
  position: absolute;
  height: 45px;
  max-height: 45px;
  width: 40px;
  margin: 0;
  cursor: pointer;
}

.inputArrowRight {
  @include arrow-style;
  right: 0;
  transform: rotate(180deg);
}

.inputArrowLeft {
  @include arrow-style;
  left: 0;
}

.minesInput {
  & input {
    text-align: center;
    &::selection {
      background: transparent;
    }
  }
}
