@import "sass/index";
@import "config/themes/_mines";

.switchWrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  & > span:first-child {
    margin-right: 10px;
  }
  & > span:last-child {
    margin-left: 10px;
  }
}

.option {
  text-transform: uppercase;
  color: use(--template-gameCell-switch-text);
  font-size: 14px;
  font-weight: bold;
  cursor: pointer;
  user-select: none;
}

.highlight {
  color: use(--template-gameCell-switch-selectedText);
}

.switchToggle {
  width: 16px;
  height: 16px;
  top: 2px;
}

.switchTrack {
  height: 13px;
}
